@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
* {
    box-sizing: border-box;
}

body{
    background-color: #111;
    color: #FFF;
    margin: 0;
    font-family: 'Roboto', sans-serif;
}

.lists{
    margin-top: -140px;
}

.loading{
    position: fixed;
    height: 100vh;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 99;
    background-color: #000;
    display: flex;
    justify-content: center;
    align-items: center;
}
.movieRow{
    margin-bottom: 30px;
}

.movieRow h2{
    margin: 0px 0px 0px 30px;
}

.movieRow-listArea{
    overflow-x: hidden;
    padding-left: 30px;
}

.movieRow-list{
    transition: all ease 1s;
}

.movieRow--item{
    display: inline-block;
    width: 150px;
    cursor: pointer;
}

.movieRow--item img{
    width: 100%;
    transform: scale(0.9);
    transition: all ease 0.2s;
}

.movieRow--item img:hover{
    transform: scale(1);
}

.movieRow-left,
.movieRow-right{
        position: absolute;
        width: 40px;
        height: 225px;
        background-color: rgba(0, 0, 0, 0.6);
        z-index: 99;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        cursor: pointer;
        opacity: 0;
        transition: all ease 0.6s;
    }

.movieRow-left{
    left: 0;
}

.movieRow-right{
    right: 0;
}

.movieRow:hover .movieRow-left,
.movieRow:hover .movieRow-right{
    opacity: 1;
}

@media (max-width:760px){
    .movieRow-left,
    .movieRow-right{
        opacity: 1;
    }
}
.featured{
    height: 100vh;
}

.featured-vertical{
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%)
}

.featured-horizontal{
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 30px;
    padding-bottom: 150px;
    padding-top: 70px;
}

.featured-name{
    font-size: 60px;
    font-weight: bold;
}

.featured-info{
    font-size: 18px;
    font-weight: bold; 
    margin-top: 15px;
}

.featured-points, 
.featured-year, 
.featured-seasons{
    display: inline-block;
    margin-right: 15px;
}

.featured-points{
    color: #46d369;
}

.featured-description{
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    max-width: 40%;
}

.featured-buttons{
    margin-top: 25px;
}

.featured-button-assistir, .featured-button-minhalista{
    display: inline-block;
    font-size: 20;
    font-weight: bold;
    padding: 12px 25px;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;

}

.featured-button-assistir{
    background-color: #FFF;
    color: #000;
    transition: 0.3s;
}

.featured-button-minhalista{
    background-color: #333;
    color: #FFF;
    transition: 0.3s;
}

.featured-button-assistir:hover,
.featured-button-minhalista:hover{
    opacity: 70%;
}

.featured-genres{
    margin-top: 15px;
    font-size: 18px;
    color: #999;
}

@media (max-width:760px){
    .featured{
        height: 90vh;
    }
    .featured-name{
        font-size: 40px;
    }
    .featured-info{
        font-size: 16px;
    }
    .featured-description{
        font-size: 14px;
        max-width: 100%;
        margin-right: 30px;
    }
    .featured-button-assistir, .featured-button-minhalista{
        font-size: 16px;
    }
    .featured-genres{
        font-size: 14px;
    }
}

@media (max-width:1280px){
    .featured{
        height: 95vh;
    }
    .featured-name{
        font-size: 50px;
    }
    .featured-info{
        font-size: 17px;
    }
    .featured-description{
        font-size: 17px;
        max-width: 50%;
        margin-right: 30px;
    }
    .featured-button-assistir, .featured-button-minhalista{
        font-size: 17px;
    }
    .featured-genres{
        font-size: 15px;
    }
}
header{
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    background: transparent;
    transition: all ease 0.6s;
}

header.black{
    background-color: #141414;
}

.header-logo{
    height: 60px;
}

.header-logo img{
    height: 100%;
}

.header-usuario{
    height: 60px;
}

.header-usuario img{
    height: 100%;
    border-radius: 15px;
}


footer{
    font-size: 20px;
    text-align: center;
    margin: 60px 0;
}
