.featured{
    height: 100vh;
}

.featured-vertical{
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%)
}

.featured-horizontal{
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 30px;
    padding-bottom: 150px;
    padding-top: 70px;
}

.featured-name{
    font-size: 60px;
    font-weight: bold;
}

.featured-info{
    font-size: 18px;
    font-weight: bold; 
    margin-top: 15px;
}

.featured-points, 
.featured-year, 
.featured-seasons{
    display: inline-block;
    margin-right: 15px;
}

.featured-points{
    color: #46d369;
}

.featured-description{
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    max-width: 40%;
}

.featured-buttons{
    margin-top: 25px;
}

.featured-button-assistir, .featured-button-minhalista{
    display: inline-block;
    font-size: 20;
    font-weight: bold;
    padding: 12px 25px;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;

}

.featured-button-assistir{
    background-color: #FFF;
    color: #000;
    transition: 0.3s;
}

.featured-button-minhalista{
    background-color: #333;
    color: #FFF;
    transition: 0.3s;
}

.featured-button-assistir:hover,
.featured-button-minhalista:hover{
    opacity: 70%;
}

.featured-genres{
    margin-top: 15px;
    font-size: 18px;
    color: #999;
}

@media (max-width:760px){
    .featured{
        height: 90vh;
    }
    .featured-name{
        font-size: 40px;
    }
    .featured-info{
        font-size: 16px;
    }
    .featured-description{
        font-size: 14px;
        max-width: 100%;
        margin-right: 30px;
    }
    .featured-button-assistir, .featured-button-minhalista{
        font-size: 16px;
    }
    .featured-genres{
        font-size: 14px;
    }
}

@media (max-width:1280px){
    .featured{
        height: 95vh;
    }
    .featured-name{
        font-size: 50px;
    }
    .featured-info{
        font-size: 17px;
    }
    .featured-description{
        font-size: 17px;
        max-width: 50%;
        margin-right: 30px;
    }
    .featured-button-assistir, .featured-button-minhalista{
        font-size: 17px;
    }
    .featured-genres{
        font-size: 15px;
    }
}